.checkoutProduct{
    display: flex;
    margin-top:20px;
    margin-bottom:20px;
}

.checkoutProduct_info{
    padding-left: 20px;
}

.checkoutProduct_info>button{
    background: #f0c14b;
    border:1px solid;
    margin-top:10px;
    border-color: #a88734   #f0c14b #846a29;
    color: black;
}

.checkoutProduct_image{
    object-fit: contain;
    width: 180px;
    height: 180px;
}

.checkoutProduct_rating{
    display: flex;
}

.checkoutProduct_title{
    font-size:17px;
    font-weight: 800;
}