.login{

    display:flex;
    height:100vh;
    background: white;
    align-items:center;
    flex-direction: column;
}
.login_logo{
   margin-top: 20px;
   margin-bottom: 20px;
   object-fit:contain;
   width:100px;
   margin-right:auto;
   margin-left: auto;
   z-index: 100;
}
.login_container{
    width:300px;
    height:fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border:1px solid darkgray;
    padding: 20px;
}

h1 {
    font-weight: 500;
    margin-bottom: 20px;
}

h5 {
    margin-bottom: 5px;
}

.text {
    height:30px;
    margin-bottom: 10px;
    background-color: white;
    width:98%;
}

p{
    margin-top:15px;
    font-size:12px;
}

.login_signInButton {
    background: #f0c14b;
    border-radius:2px;
    width:100%;
    height:30px;
    border:1px solid;
    margin-top:10px;
    border-color:#a88734 #9c7e31 #846a29
}

.login_registerButton {
    border-radius:2px;
    width:100%;
    height:30px;
    border:1px solid;
    margin-top:10px;
    border-color:darkgray;
}

